import React, { Component } from "react";
import ListSubheader from "@material-ui/core/ListSubheader";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import Banner from "../elements/element-banner";

function NestedList(props) {
  const [open, setOpen] = React.useState(props.open);

  const handleClick = () => {
    setOpen(!open);
    console.log(open);
  };

  return (
    <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={<ListSubheader component="div" id="nested-list-subheader" />}
    >
      <ListItem button onClick={handleClick}>
        <ListItemText primary={props.text} />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <div className="info-item">{props.form}</div>
        </List>
      </Collapse>
    </List>
  );
}
var openC = false;
var openG = false;
var openZ = false;

export default class Info extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.handleChange = this.handleChange.bind(this);
  }

  componentWillMount() {
    console.log(this.props);
    if (this.props.location.pathname) {
      var key = this.props.location.pathname;
      key = key.replace("/info/", "");
      console.log(key);
      if (key === "gastos") {
        openG = true;
        openC = false;
        openZ = false;
      }
      if (key === "time") {
        openG = false;
        openC = true;
        openZ = false;
      }
      if (key === "delivery") {
        openG = false;
        openC = false;
        openZ = true;
      }
    }
  }

  handleChange = (event) => {};

  clickHandler = () => {
    this.setState({ button: !this.state.button });
  };

  render() {
    return (
      <section className="faq-section section-b-space">
        <div className="container">
          <Banner />
          <div className="row">
            <div className="col-sm-12">
              <NestedList
                className="card-header"
                text="Condiciones Generales"
                form={condiciones}
                open={openC}
              />
              <NestedList
                className="card-header"
                text="Compromiso de compra-venta"
                form={compromiso}
                open={false}
              />

              <NestedList
                className="card-header"
                text="Proteccin de Datos de Carcter Personal"
                form={proteccion}
                open={false}
              />

              <NestedList
                className="card-header"
                text="Lugar de entrega"
                form={lugar}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Zonas de reparto"
                form={zonas}
                open={openZ}
              />
              <NestedList
                className="card-header"
                text="Gastos de entrega"
                form={gastos}
                open={openG}
              />

              <NestedList
                className="card-header"
                text="Importe de la compra"
                form={importe}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Productos agotados"
                form={agotados}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Devoluciones de productos"
                form={devoluciones}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Desistimiento"
                form={destinamiento}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Garanta"
                form={garantia}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Quejas y reclamaciones"
                form={quejas}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Seguridad en las transacciones"
                form={seguridad}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Condiciones denerales de uso de la pgina web"
                form={condiciones}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Google Analytics"
                form={google}
                open={false}
              />
              <NestedList
                className="card-header"
                text="Generales"
                form={generales}
                open={false}
              />
            </div>
          </div>
        </div>
      </section>
    );
  }
  scrollToMyRef = () => window.scrollTo(0, this.myRef.offsetTop);
}

